.slide-img {
  width: 100%;
  height: auto;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  align-content: center;
  justify-content: center;
  text-align: center; /* para centrar horizontalmente */
  background: #222;
}

.slide-img -img {
  max-width: 100%;
  height: auto;
  flex-basis: 100%;
  align-self: center;
  display: flex; /* para centrar verticalmente */
  justify-content: center; /* para centrar verticalmente */
}