.fotter-light {
background: #C4C4C4;

}


.fotter-dark {
background:#423F3F;
}

.container-footer {
margin-left: 10vh;
margin-right: 10vh;
background:linear-gradient(#12897A,#4e3404);
}

.container-footer img {
  margin-left: 3vh;
  margin-right: 3vh;
  max-width: 15%;
}


