*/


.logo {
    width: 100px;
  height: 100px;
  margin: 10px;

  transition: transform 0.3s ease;
}

.logo:hover {
transform: scale(1.5);
  transition: transform 0.3s ease-in-out;}



@media screen and (max-width: 768px) {

.container-footer {
margin-left: 1vh;
margin-right: 1vh;
background:linear-gradient(#12897A,#4e3404);
}

}













