.table {
  display: table;
  width: 100%;
}

.row {
  display: table-row;
}

.cell {
  display: table-cell;
  padding: 0.5em;
  border: 1px solid #ccc;
  font-size: 0.7em;
}

.header .cell {
  font-weight: bold;
  background-color: #eee;
}
.titulo_Carrera{
 font-size: 0.6em;
 
  overflow: hidden;
}

.table span{
  background-color: #eee;

}

.table h5{
  color: #12897A;
}

.table p{
  padding: 5px;
}
