.modales {
  position: fixed;
  z-index: 9999;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
  justify-content: center;
  align-items: center;
}

.modales-content {
  background-color: #222;
  margin: 10% auto;
  padding: 10px;
  border: 1px solid #888;
  width: 100%;
  max-width: 75%;
  position: relative;
}

.modales-content button {
  position: absolute;
  top: 5px;
  right: 10px;
  font-size: 16px;
  font-weight: bold;
  color: #555;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.modales-content img {
  width: 15%;
  height: auto;
  padding: 2px;
  margin-bottom: 10px;
}

.modales-content .modales-conten{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.modales-content h3 {
  display: block;
  font-size: 24px;
  font-weight: bold;
  text-align: center;
  margin: px 0;
  color: #FFC107;
  text-shadow: 1px 1px #fff;
  text-transform: uppercase;
}

