/* Estilos para pantallas pequeñas */
@media (max-width: 576px) {
  h3 {
    font-size: 1.5rem;
  }
}

/* Estilos para pantallas medianas */
@media (min-width: 577px) and (max-width: 992px) {
  h3 {
    font-size: 1.8rem;
  }
}

/* Estilos para pantallas grandes */
@media (min-width: 993px) {
  h3 {
    font-size: 2rem;
  }
}