.link-initiallight {
  display: grid;
  justify-content: end;
  /*border: 3px solid #D6EAF8;*/
  /*background-color:#167CBB;*/
 margin-left: 10vh;
 margin-right: 1vh;

}




.link-initiallight ul {
  display: flex;
  padding: 0;

  align-items: center;
   /*background-color:#167CBB;*/
}

.link-initiallight li {
 list-style-type: none;

  margin: 0 1rem;

}

.link-initiallight li a {
 text-decoration: none;

  display: block;
  width: 100%;
}



.link-initialdark {
  display: grid;
  justify-content: end;
  /*border: 3px solid #167CBB;*/

  /*background-color:#423F3F;*/

  margin-left: 10vh;
 margin-right: 1vh;

}




.link-initialdark ul {
  display: flex;
  padding: 0;

  align-items: center;
   /*background-color:#423F3F;*/

}
.link-initialdark li {
  
  list-style-type: none;

  margin: 0 1rem;
}
.link-initialdark li a {

  text-decoration: none;
  display: block;
  width: 100%;
}







@media screen and (max-width: 768px) {


.link-initlight {
  display: grid;
  justify-content: end;
  /*border: 3px solid #D6EAF8;*/
  /*background-color:#167CBB;*/
 margin-left: 1vh;
 margin-right: 1vh;

}

.link-initdark {
  display: grid;
  justify-content: end;
  /*border: 3px solid #167CBB;*/

  /*background-color:#423F3F;*/

  margin-left: 1vh;
 margin-right: 1vh;

}

}












 















